*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  font-family: system-ui;
  font-size: 1.125rem;
  line-height: 1.6;
  padding: 0 0.5rem;
}
body {
  background: #141414;
  z-index: -3; 
  overflow-x: hidden;
}

.text {
  max-width: 1000px;
  min-width: 300px;
  padding: 1rem 0;
  margin: 1rem 0;
  background: #292929;
  position: relative;
  z-index: 0;
  border-radius: 8px;
  overflow: hidden;
  padding: 0.25rem;
  font-family: sans-serif;
  display: grid;
  place-items: center;
  grid-area: root;
}


.text-content {
  font-size: 0.75em;
  font-weight: 600;
  align-self: center;
  font-family: system-ui;
  color: #fdfdfe;
  justify-self: center;
  padding: 1rem;
}

@keyframes rotate {
  100% {
    transform: rotate(1turn);
  }
}

.text::before {
  content: "";
  position: absolute;
  z-index: -2;
  left: -50%;
  top: -50%;
  width: 200%;
  height: 200%;
  background-color: #1a232a;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-image: conic-gradient(transparent, #e0e0e0, transparent 30%);
  animation: rotate 6s linear infinite;
}

.text::after {
  content: "";
  position: absolute;
  z-index: -1;
  left: 2px;
  top: 2px;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  background: #000;
  border-radius: 8px;
  animation: opacityChange 5s infinite linear;
}
img {
  max-width: 100%;
  display: block;
}
.wrapper {
  max-width: 40rem;
  margin-inline: auto;
}

.accordion {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 1rem;
  --_button-size: 3rem;
  --_panel-padding: 0.75rem;
  --_panel-gap: 1rem;
}
@media (min-width: 45em) {
  .accordion {
      flex-direction: row;
      height: 20rem;
  }
}
.accordion * {
  margin: 0;
}
.accordion-panel {
  position: relative;
  isolation: isolate;
  flex-basis: calc((var(--_panel-padding) * 2) + var(--_button-size));
  overflow: hidden;
  padding: var(--_panel-padding);
  padding-right: calc(var(--_panel-padding) * 4);
  border-radius: calc(((var(--_panel-padding) * 2) + var(--_button-size)) / 2);
  transition: flex-basis 500ms, flex-grow 500ms;
}
.accordion-panel:nth-child(1) {
  --_panel-color: hsl(188, 79%, 41%);
}
.accordion-panel:nth-child(2) {
  --_panel-color: hsl(125, 70%, 50%);
}
.accordion-panel:nth-child(3) {
  --_panel-color: pink;
}
.accordion-panel:nth-child(4) {
  --_panel-color: hsla(0, 2%, 82%, 0.5);
}
.accordion-panel:has([aria-expanded="true"]) {
  flex-basis: clamp(15rem, 40vh, 20rem);
  flex-grow: 1;
}
.accordion-trigger {
  outline: 0;
}
.accordion-panel:focus-within {
  outline: 3px solid var(--_panel-color);
  outline-offset: 4px;
}
.accordion-content > p {
  transform: translateY(2rem);
  opacity: 0;
  color: #e0e0e0;
  margin-left: calc(var(--_button-size) + var(--_panel-gap));
}
.accordion-panel:has([aria-expanded="true"]) p {
  transition: transform 500ms 500ms, opacity 500ms 500ms;
  transform: translateY(0);
  opacity: 1;
}
.accordion-title {
  font-size: 1.5rem;
  font-weight: 700;
  position: relative;
  isolation: isolate;
  display: grid;
  align-items: center;
  color: #141414;
}
@media (max-width: 44.999em) {
  .accordion-title::after {
      content: "";
      position: absolute;
      left: calc((var(--_panel-gap) + var(--_button-size)) * -1);
      width: calc(100% + (var(--_button-size) * 2));
      height: var(--_button-size);
      background: hsla(0, 2%, 82%, 0.5);
      z-index: -1;
      border-radius: 100vw;
  }
  #blob {
    display: none;
  }
  html, body {
    overflow: auto;
  }
  #profile_pic {
    display: none
  }
}
@media (min-width: 44.999em) {
  .accordion-panel:has([aria-expanded="true"]) 
  .accordion-title::after {
      content: "";
      position: absolute;
      left: calc((var(--_panel-gap) + var(--_button-size)) * -1);
      width: calc(100% + (var(--_button-size) * 2));
      height: var(--_button-size);
      background: hsla(0, 2%, 82%, 0.5);
      z-index: -1;
      border-radius: 100vw;
  }
}
@media (min-width: 1370px) {
  .container {
    display: grid;
    grid-template-areas: 
    "left-side left-side left-side left-side"
    "app app accordion accordion"
    "app app accordion accordion";
    gap: 1rem;
    padding: 1rem 2rem;
    align-items: center;
    justify-items: center;
    overflow: hidden;
    position: relative;
  }
  
  .left-side {
    grid-area: left-side;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 3rem;
    width: 100%;
    padding-bottom: calc(100vh / 10);
  }
  #welcomeTextContainer {
    display: flex; 
    align-items: center;
  }
  #profile_pic {
    width: 80%;
    aspect-ratio: 1;
  }

  #root {
    grid-area: app;
  }

  .wrapper {
    grid-area: accordion;
  }
  

  #welcome-text {
    font-size: 3rem;
  }
  .text-content {
    font-size: 1.15em;
  }
  .carousel {
    width: 7.5rem;
    height: 7.5rem;
  }
  .nav {
    font-size: 5rem;
  }
  .card {
    font-size: 1.125rem;
    padding: 2rem;
  }

  .carousel {
    width: 10rem;
    height: 10rem
  }

  #blur {
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: -1;
    backdrop-filter: blur(12vmax);
    background-size: fit;
  }

  body, html {
    padding: 0;
  }
  body {
    overflow: hidden;
    margin: 0;
  }

}
@media (max-width: 1369.999px) {
  #blob {
    display: none;
  }
  #blur {
    display: none;
  }
  .left-side {
    grid-area: left-side;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    padding: 1rem;
  }
}
.accordion-image {
  position: absolute;
  inset: 0;
  object-fit: cover;
  width: 100%;
  height: 100%;
  z-index: -1;
  transition: filter 500ms;
}
.accordion-panel:has([aria-expanded="true"]) .accordion-image {
  filter: brightness(0.5);
}
.accordion-trigger {
  background: transparent;
  border: 0;
  display: flex;
  align-items: center;
  gap: var(--_panel-gap);
  flex-direction: row-reverse;
  padding: 0;
}
.accordion-icon {
  fill: var(--_panel-color);
  background: hsl(0 0% 0% / 0.55);
  width: var(--_button-size);
  aspect-ratio: 1 / 1;
  padding: 0.75rem;
  border-radius: 50%;
  z-index: 1;
}
.contactSvg {
  fill: var(--_panel-color);
  background: hsl(0 0% 0% / 0.55);
  width: var(--_button-size);
  aspect-ratio: 1 / 1;
  padding: 0.25rem;
  border-radius: 50%;
  z-index: 1;
}

a::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  background-color: #e0e0e0;
  bottom: 0;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform .3s ease-in-out;
}

a:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}

a {
text-decoration: none;
color: #e0e0e0;
font-weight: bold;
position: relative;
font-smooth: always;
}
@keyframes rotate {
  from {
    rotate: 0deg;
  }
  
  50% {
    scale: 1 1.5;
  }
  
  to {
    rotate: 360deg;
  }
}
#blob {
  background-color: white;
  height: 30vmax;
  aspect-ratio: 1;
  position: absolute;
  left: 50%;
  top: 50%;
  translate: -50% -50%;
  border-radius: 50%;
  background: linear-gradient(to right, aquamarine, mediumpurple);
  animation: rotate 20s infinite;
  opacity: 0.8;
  z-index: -2;
}

#profile_pic {
  width: 10rem;
  border-radius: 0.5rem;
  box-shadow: 0 0 0.75rem #e0e0e0;
  aspect-ratio: initial;
}

.carousel {
  position: relative;
  width: 7.5rem;
  height: 7.5rem;
  perspective: 500px;
  transform-style: preserve-3d;
  grid-area: carousel;
}
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center
}

.card-container {
  position: absolute;
  width: 100%;
  height: 100%;
  transform: 
    rotateY(calc(var(--offset) * 50deg)) 
    scaleY(calc(1 + var(--abs-offset) * -0.4))
    translateZ(calc(var(--abs-offset) * -30rem))
    translateX(calc(var(--direction) * -5rem));
  filter: blur(calc(var(--abs-offset) * 1rem));
  transition: all 0.3s ease-out;
}

.card {
  width: 100%;
  height: 100%;
  padding: 1rem;
  background-color: hsl(280deg, 40%, calc(100% - var(--abs-offset) * 50%));
  border-radius: 1rem;
  display: flex;
  justify-content: center; 
  align-items: center; 
  transition: all 0.3s ease-out;

  
  p {
    transition: all 0.3s ease-out;
    opacity: var(--active);
    font-size: 1.125rem;
  }
}

.nav {
  color: white;
  font-size: 3rem;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 50%;
  z-index: 2;
  cursor: pointer;
  user-select: none;
  background: unset;
  border: unset;
  
  &.left {
    left: 0;
    transform: translateX(-100%) translatey(-50%);
  }
  
  &.right {
    right: 0;
    transform: translateX(100%) translatey(-50%);
  }
}
@media (max-width: 425px) {
  .nav {
    display: none;
  }
}

#welcome-text {
  font-size: 1.5rem;
  font-weight: 600;
  color: #fdfdfe;
  text-shadow: 0px 0px 2.5px #b393d3, 0px 0px 5px #b393d3, 0px 0px 5px #b393d3,
    0px 0px 10px #b393d3;
}
#email {
  font-weight: bold;
}